.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*, ::after, ::before {
    box-sizing: border-box;
}

.map-container {
  position: absolute;
  top: 150px;
  right: 200px;
  left: 200px;
  bottom: 150px;
}

.layer-selectbox{
  width: 25%;
  font-size: 17px;
  left: 100px
}
.chart-header{
  margin-bottom: 60px;
}
.section{

}

#myFrontPage{
  margin-top: -50px;
  display: flex;
}
.app-fullpage {
  background-color: white;
  min-height: 100vh;
  display: block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  position: relative;
}

.app-footer{
  background-color: rgba(32, 33, 36, 1);
  min-height: 1vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgba(232, 234, 237, 1);
}


/**
* Set rules for how the map overlays
* (information box and legend) will be displayed
* on the page. */
.map-overlay {
  position: absolute;
  bottom: 150px;
  right: 200px;
  background: rgba(255, 255, 255, 0.8);
  margin-right: 20px;
  font-family: Arial, sans-serif;
  overflow: auto;
  border-radius: 3px;
  font-size: 12px;
}

#features {
  top: 150px;
  right: 200px;
  height: 300px;
  margin-top: 20px;
  width: 250px;
  font-size: 12px;
}

#legend {
  padding: 10px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  line-height: 18px;
  height: 150px;
  margin-bottom: 40px;
  width: 100px;
}

#selectLayer{

}

.exp-text{
  font-size: 15px;
  padding: 20px
}

.legend-key {
  display: inline-block;
  border-radius: 20%;
  width: 10px;
  height: 10px;
  margin-right: 5px;
}
/* Style the header */
.header {
  padding: 10px 16px;
  
}

.app-header {
  display: flex;
  z-index: 1;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  height: 50px;
  line-height: 20px;
  /*background-color: ;*/
  font-size: 40px;
  /*color: black;*/
  color: #ffd36b;
	transition: all 1s linear;
}


/* Page content */
.content {
  padding: 16px;
}

/* The sticky class is added to the header with JS when it reaches its scroll position */
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgba(32, 33, 36, 1);
  color: rgba(232, 234, 237, 1);
}

@keyframes mymove {
  from {background-color: red;}
  to {background-color: blue;}
}

/* Add some top padding to the page content to prevent sudden quick movement (as the header gets a new position at the top of the page (position:fixed and top:0) */
.sticky + .content {
  padding-top: 102px;
}


.header-menu-item{
	font-weight: bold;
	font-size: 18px;
	padding: 5px 50px;
  width: 150px;
  box-sizing: border-box;
}


.header-inner-container{
	position: relative;
	box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
	width: 100%;
	height: 100%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}
.section-header{
	padding: 5px 0;
	font-size: 30px;
	font-weight: bold;
}
.space-window{
    /*background: url(img/meexplaining.jpg);*/
    background-size: cover !important;
    background-position: 50% !important;
    background-repeat: no-repeat !important;
    background-attachment: fixed !important;
    height: 250px;
    width: 100%;
    transition: 0.3s;
    display: block;

}
